*{
    box-sizing: border-box;
}
body{
    margin:0;
    padding:0;
    
    background-color:#eff3f7;
    height:100%;
}

html, body, #potato, .container{
    height:100%;    
    display: flex;
}

.loader {
    width:100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%; 
    padding-top: 70px;
}

.movies .movie { 
    width: 45%;
    background-color: white;
    margin-bottom: 50px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    position: relative;
    color: #abaeb9;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie img{
    position: relative;
    top:-50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie .movie_title,
.movie .movie_year { 
    margin:0;
    font-weight: 300;
}

.movie .movie_title {
    margin-bottom:5px;
    font-size: 24px;
    color:#2c2c2c;
}

.movie .movie_genres { 
    list-style: none;
    padding:0;
    margin:0;
    display: flex;
    margin:5px 0;
}

.movie_genres li{
    margin-right: 0;
    font-size: 14px;
}